"use client"

import { useState, useEffect, useMemo } from "react"
import { Card } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import { Star, BookMarked, Loader2, Save, BookOpen } from "lucide-react"
import { getReadingData } from "@/lib/api-client"

export function MonthReview({ month, userEmail, monthIndex, year }: any) {
  const [completedBooks, setCompletedBooks] = useState<any[]>([])
  const [loading, setLoading] = useState(true)
  const [bookEdits, setBookEdits] = useState<Record<string, { cover: string, rating: number, pages: number }>>({})
  const [isSaving, setIsSaving] = useState<string | null>(null)

  useEffect(() => {
    async function loadFinishedBooks() {
      if (!userEmail) return
      setLoading(true)
      try {
        const data = await getReadingData(userEmail, year, monthIndex + 1)
        const finished = data.filter((r: any) => r.status === "finalizado")
        
        setCompletedBooks(finished)

        const initialEdits: any = {}
        finished.forEach((b: any) => {
          initialEdits[b.book_name] = {
            cover: b.cover_url || "",
            rating: b.rating || 0,
            pages: b.total_pages || 0
          }
        })
        setBookEdits(initialEdits)
      } catch (err) {
        console.error(err)
      } finally {
        setLoading(false)
      }
    }
    loadFinishedBooks()
  }, [userEmail, monthIndex, year])

  const stats = useMemo(() => {
    const totalBooks = completedBooks.length
    const totalPages = completedBooks.reduce((acc, book) => acc + (Number(bookEdits[book.book_name]?.pages) || 0), 0)
    return { totalBooks, totalPages }
  }, [completedBooks, bookEdits])

  const handleSave = async (bookName: string) => {
    setIsSaving(bookName)
    try {
      const data = bookEdits[bookName]
      const response = await fetch("/api/reading-data", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          action: "UPDATE_REVIEW",
          email: userEmail,
          bookName: bookName,
          rating: data.rating,
          coverUrl: data.cover,
          totalPages: data.pages
        }),
      })

      if (response.ok) {
        alert("Dados salvos com sucesso!")
      }
    } catch (err) {
      alert("Erro ao salvar.")
    } finally {
      setIsSaving(null)
    }
  }

  if (loading) return <div className="flex justify-center p-10"><Loader2 className="animate-spin text-primary" /></div>

  return (
    <Card className="p-6 bg-card border-accent/20 shadow-xl">
      <div className="text-center mb-6">
        <h2 className="text-2xl font-bold flex items-center justify-center gap-2">
          <BookMarked className="text-primary" /> Histórico de {month} / {year}
        </h2>
      </div>

      <div className="grid grid-cols-2 gap-4 mb-8 bg-primary/5 p-4 rounded-xl border border-primary/10">
        <div className="flex flex-col items-center justify-center">
          <span className="text-[10px] uppercase font-bold text-muted-foreground">Livros Lidos</span>
          <span className="text-3xl font-black text-primary">{stats.totalBooks}</span>
        </div>
        <div className="flex flex-col items-center justify-center border-l border-primary/10">
          <span className="text-[10px] uppercase font-bold text-muted-foreground">Páginas Lidas</span>
          <span className="text-3xl font-black text-primary">{stats.totalPages}</span>
        </div>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        {completedBooks.map((book, idx) => (
          <div key={idx} className="flex gap-4 p-4 border rounded-lg bg-background/40">
            <div className="w-24 h-36 bg-muted rounded shadow-md overflow-hidden flex-shrink-0 border">
              {bookEdits[book.book_name]?.cover ? (
                <img src={bookEdits[book.book_name].cover} className="w-full h-full object-cover" />
              ) : (
                <div className="h-full flex items-center justify-center text-[9px] text-center p-2 uppercase font-bold opacity-50">Sem Capa</div>
              )}
            </div>

            <div className="flex-1 flex flex-col">
              <h3 className="font-black text-xs uppercase text-primary truncate w-full mb-2">{book.book_name}</h3>
              
              <Input 
                placeholder="Link da Capa (URL)"
                className="h-7 text-[10px] mb-2"
                value={bookEdits[book.book_name]?.cover || ""}
                onChange={(e) => setBookEdits(prev => ({...prev, [book.book_name]: {...prev[book.book_name], cover: e.target.value}}))}
              />

              <div className="flex items-center gap-2 mb-3">
                <BookOpen size={12} className="text-primary" />
                <Input 
                  type="number"
                  placeholder="Páginas"
                  className="h-7 w-20 text-[10px]"
                  value={bookEdits[book.book_name]?.pages || ""}
                  onChange={(e) => setBookEdits(prev => ({...prev, [book.book_name]: {...prev[book.book_name], pages: Number(e.target.value)}}))}
                />
                <span className="text-[10px] text-muted-foreground">páginas</span>
              </div>

              <div className="mt-auto flex items-center justify-between border-t pt-2">
                <div className="flex gap-1">
                  {[1,2,3,4,5].map(s => (
                    <Star 
                      key={s} size={14} 
                      className={`cursor-pointer ${s <= (bookEdits[book.book_name]?.rating || 0) ? "fill-primary text-primary" : "text-border"}`}
                      onClick={() => setBookEdits(prev => ({...prev, [book.book_name]: {...prev[book.book_name], rating: s}}))}
                    />
                  ))}
                </div>
                <Button size="sm" className="h-8 w-8 rounded-full" onClick={() => handleSave(book.book_name)} disabled={isSaving === book.book_name}>
                  {isSaving === book.book_name ? <Loader2 className="animate-spin" size={12} /> : <Save size={14} />}
                </Button>
              </div>
            </div>
          </div>
        ))}
      </div>
    </Card>
  )
}